import React, { useEffect } from "react";
import styles from "./CommunityPage.module.css";
import { useState } from "react";

const CommunityPage = () => {
    const [activeTab, setActiveTab] = useState("notice");
    const [freeboardData, setFreeboardData] = useState([]);

    useEffect(() => {
        fetch(process.env.PUBLIC_URL + "/data/freeboardData.json")
            .then(res => res.json())
            .then(data => setFreeboardData(data));
    }, []);

    return (
        <div className={styles.container}>
            <div className={styles.tabs}>

                <button className={activeTab === "notice" ? styles.active : ""}
                    onClick={() => setActiveTab("notice")}
                >

                     FAQ
                </button>

                <button className={activeTab === "freeboard" ? styles.active : ""}
                    onClick={() => setActiveTab("freeboard")}
                >
                    
                </button>
            </div>


            <div className={styles.content}>
                {activeTab === "notice" &&

                    (
                        <>
                            <p className={styles.main_title}>    !</p>
                            <div className={styles.select_box}>
                                <label></label>
                                <select id="notice_category" name="notice_category">
                                    <option value="all"></option>
                                    <option value="delivery"></option>
                                    <option value="cancel">/</option>
                                    <option value="pay"></option>
                                    <option></option>
                                </select>
                            </div>


                            <table className={styles.notice_table}>
                                <thead>
                                    <tr>
                                        <td></td>
                                        <td></td>
                                        <td></td>
                                        <td></td>
                                        <td></td>
                                        <td></td>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr>
                                        <td>1</td>
                                        <td>    </td>
                                        <td></td>
                                        <td></td>
                                        <td>2024-04-18</td>
                                        <td>11485</td>
                                    </tr>
                                </tbody>
                            </table>


                            <div className={styles.table_bottom}>

                                <div className={styles.duration}>
                                    <label></label>
                                    <select id="duration_category" name="duration_category">
                                        <option value="week"></option>
                                        <option value="month"></option>
                                        <option value="months"></option>
                                        <option value="all"></option>
                                    </select>
                                </div>

                                <div className={styles.title}>
                                    <label></label>
                                    <select id="title_category" name="title_category">
                                        <option value="title"></option>
                                        <option value="content"></option>
                                        <option value="writer"></option>
                                        <option value="nickname"></option>
                                    </select>
                                </div>
                                <div className={styles.search_box}>
                                    <input type="search" />
                                    <button className={styles.search_button}></button>

                                </div>

                            </div>
                        </>
                    )
                }
                {activeTab === "freeboard" &&

                    (

                        <>

                            <p className={styles.main_title}>  </p>
                            <div className={styles.select_box}>
                                <label></label>
                                <select id="notice_category" name="notice_category">
                                    <option value="all"></option>
                                    <option value="delivery"></option>
                                    <option value="cancel">/</option>
                                    <option value="pay"></option>
                                    <option></option>
                                </select>
                            </div>


                            <table className={styles.notice_table}>
                                <thead>
                                    <tr>
                                        <td></td>
                                        <td></td>
                                        <td></td>
                                        <td></td>
                                        <td></td>
                                        <td></td>
                                    </tr>
                                </thead>
                                <tbody>
                                    {freeboardData.map((item) => (
                                        <tr key={item.id}>
                                            <td>{item.id}</td>
                                            <td>
                                                <img src={process.env.PUBLIC_URL +  item.productImage}
                                                    alt={item.title}
                                                    style={{
                                                        width: "60px",
                                                        height: "60px",
                                                        objectFit: "cover",
                                                        borderRadius: "10px",
                                                    }} />
                                            </td>
                                            <td>{item.title}</td>
                                            <td>{item.writer}</td>
                                            <td>{item.date}</td>
                                            <td>{item.views}</td>
                                        </tr>



                                    ))}
                                </tbody>
                            </table>
                            <div className={styles.table_bottom}>
                                <div className={styles.duration}>
                                    <label></label>
                                    <select id="duration_category" name="duration_category">
                                        <option value="week"></option>
                                        <option value="month"></option>
                                        <option value="months"></option>
                                        <option value="all"></option>
                                    </select>
                                </div>

                                <div className={styles.title}>
                                    <label></label>
                                    <select id="title_category" name="title_category">
                                        <option value="title"></option>
                                        <option value="content"></option>
                                        <option value="writer"></option>
                                        <option value="nickname"></option>
                                    </select>
                                </div>
                                <div className={styles.search_box}>
                                    <input type="search" />
                                    <button className={styles.search_button}></button>

                                </div>

                            </div>



                        </>
                    )





                }
            </div>
        </div>

    );


};








export default CommunityPage;